<div className="fixed inset-0 flex items-center justify-center bg-black overflow-hidden">
  {/* Carousel container */}
  <div className="relative w-[600px] h-[700px] flex items-center justify-center">
    {posters.map((poster, index) => (
      <div key={poster.id} style={getTransform(index)}>
        <div className="w-40 h-56 relative rounded-lg overflow-hidden shadow-lg border border-white/20">
          <Image
            src={poster.src}
            alt={poster.alt}
            fill
            className="object-cover"
          />
        </div>
      </div>
    ))}
  </div>

  {/* Controls */}
  <div className="absolute bottom-20 flex space-x-10 z-20">
    <button
      onClick={prevSlide}
      className="w-14 h-14 bg-white/10 rounded-full flex items-center justify-center text-white border border-white/30 hover:bg-white/20 transition"
    >
      ‹
    </button>
    <button
      onClick={nextSlide}
      className="w-14 h-14 bg-white/10 rounded-full flex items-center justify-center text-white border border-white/30 hover:bg-white/20 transition"
    >
      ›
    </button>
  </div>
</div>
